import React from 'react';
import Axios from 'axios';
import {
  BarChart,
  Bar,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend,
  Cell,
  ResponsiveContainer
} from 'recharts';
import { Layout, Row, Select } from 'antd';
import { UNION_LEVEL } from "./Constants";
import './AppContent.css';

const { Content } = Layout;
const { Option } = Select;

export default class AppContent extends React.Component {
  state = {
    unionLevel: 6000,
    chartData: [],
  }

  componentDidMount() {
    this.loadData();
  }

  loadData = (unionLevel = 6000) => {
    Axios.get(`/api-ms/ajax/dpmrank/${unionLevel}`)
      .then(response => {
        this.setState({ chartData: response.data.dpmdict, unionLevel });
      })
  }

  handleUnionLevel = unionLevel => this.loadData(unionLevel);

  render() {

    const { unionLevel, chartData } = this.state;
    const options = UNION_LEVEL.map(item =>
        (<Option value={item} key={item.toString()}>{item}</Option>));

    console.log(chartData);

    return (
      <Content className="app-content">
        <Row>
          <span>指定UNION等级</span>
          <Select
            value={unionLevel}
            onChange={this.handleUnionLevel}
          >
            {options}
          </Select>
        </Row>
        <Row className="chart-row">
          <ResponsiveContainer width="90%" height="100%">
            <BarChart layout="vertical" data={chartData} margin={{ top: 30, right: 30, bottom: 30, left: 80 }} barSize={30} barGap="20%">
              <XAxis type="number" />
              <YAxis dataKey="name" type="category" interval={0} axisLine={false} tickLine={false}/>
              <Bar dataKey="dpm" fill="orange" />
            </BarChart>
          </ResponsiveContainer>
        </Row>
      </Content>
    );
  }
}